.threejs-journey .boy {
  pointer-events: none;
  width: 132px;
  height: 118px;
  position: absolute;
  bottom: 100%;
  left: 20px;
  overflow: hidden;
}

.threejs-journey .boy .variant {
  width: 112px;
  height: 100%;
  transition: transform .15s ease-in;
  position: absolute;
  top: 0;
  left: 0;
  transform: translateY(100%);
}

.threejs-journey .boy .variant.is-yay {
  background: url("boyYay.8daf60f2.png");
}

.threejs-journey .boy .variant.is-shrugging {
  background: url("boyShrugging.65977a74.png");
}

.threejs-journey.is-hover-yes .boy .variant.is-yay, .threejs-journey.is-hover-no .boy .variant.is-shrugging, .threejs-journey.is-hover-none .boy .variant.is-hi {
  transition-delay: .1s;
  transition-timing-function: ease-out;
  transform: translateY(0);
}

.threejs-journey .boy .is-hi .body {
  background: url("boyHiBody.8c8219f6.png");
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}

.threejs-journey .boy .is-hi .arm {
  transform-origin: 30% 90%;
  background: url("boyHiArm.66ea4bed.png");
  width: 49px;
  height: 69px;
  animation: 3s ease-in-out infinite boyArmWave;
  position: absolute;
  bottom: 0;
  right: 0;
}

@keyframes boyArmWave {
  0% {
    transform: rotate(0);
  }

  2.5% {
    transform: rotate(15deg);
  }

  7.5% {
    transform: rotate(-15deg);
  }

  12.5% {
    transform: rotate(15deg);
  }

  17.5% {
    transform: rotate(-15deg);
  }

  20% {
    transform: rotate(0);
  }

  100% {
    transform: rotate(0);
  }
}

.threejs-journey .message .bubble {
  background: linear-gradient(#fdb572ab, #fdb5721a);
  border: 2px solid #fff;
  border-radius: 8px;
  padding: 20px 30px;
  position: relative;
}

.threejs-journey .message .tip {
  background: url("bubbleTip.36543ed1.svg");
